import React from 'react'
// interface Props {
//   href: string
//   children: string
//   className: string
//   target: string
//   onClick: Function
// }
export interface IButtonProps {
  children?: React.ReactNode
  onClick?: (e: any) => void
  styles?: {}
  className?: string
  href?: string
  target?: string
}

const Button: React.SFC<IButtonProps> = props => {
  if (props.href) {
    return (
      <a className={props.className} href={props.href} target={props.target}>
        {props.children}
      </a>
    )
  }
  return (
    <button className={props.className} onClick={props.onClick}>
      {props.children}
    </button>
  )
}

export default Button

Button.defaultProps = {
  children: 'I\'m default üòÇ',
  onClick: () => {},
}
